import React, { useEffect, useRef, useState } from "react"
import '../styles/app.css'

import { Canvas, useFrame } from 'react-three-fiber'
import { softShadows, MeshDistortMaterial } from 'drei'
import { useSpring, animated } from 'react-spring/three'
import WauGradient from '../c4d/wau-sphere-texture.png'

softShadows()

const AnimatedCanvas = animated(Canvas)

const Sphere = ({position, url}) => {
  // to select the sphere component
  const sphereRef = useRef(null)
  const [hovered, setHover] = useState(false)

  // useFrame cannot be called from the global component,
  // it needs to be called from it's own extracted component that needs to be rendered
  useFrame(() => {
    if(sphereRef.current) {
      // infinite rotation animation:
      // sphereRef.current.rotation.x = sphereRef.current.rotation.y += 0.01
    }
  })

  const [props, set] = useSpring(() => ({
    scale: [1, 1, 1],
    speed: 1,
    from: {
      scale: [40, 40, 6],
    },
    delay: 1000,
    config: {
      mass: 1,
      tension: 240,
      friction: 60,
      velocity: 0
    }
  }))

  useEffect(() => {
    if(hovered) {
      set({speed: 10})
    } else {
      set({speed: 1})
    }
  }, [hovered, set])

  return (
    <animated.mesh
      castShadow
      onPointerOver={() => setHover(true)}
      onPointerOut={() => setHover(false)}
      scale={props.scale}
      position={position}
      ref={sphereRef}
      >
      {/* geomtery */}
      <sphereBufferGeometry attach='geometry' args={[1, 100, 100]}/>
      {/* meterial */}
      {/* https://github.com/pmndrs/drei#shaders */}
      <MeshDistortMaterial
        attach='material'
        color={"#403C90"}
        // drei arguments for MeshWobbleMaterial
        factor={3}
        speed={hovered ? 5 : 1}
        />
    </animated.mesh>
  )
}

const IndexPage = () => {
  const planeAnim = useSpring({
    position: [0, -3, 0],
    from: {
      position: [0, -40, 0]
    },
    delay: 600,
    config: {
      mass: 1,
      tension: 280,
      friction: 60,
      velocity: 0
    }
  })
  return (
    <>
      {/* colorManagement optimises colors */}
      {/* you can configure camera settings on the Canvas component */}
      {/* or by creating a custom camera with Drei library */}
      <AnimatedCanvas
        id="canvas-root"
        // enable shadows
        shadowMap
        colorManagement
        camera={{ position: [-5, 2, 10], fov: 50 }}
        >
        {/* lighting can be defined globally */}
        {/* directionalLight can cast shadows */}
        <directionalLight 
          // to cast shadow
          castShadow
          position={[0, 10, 0]}
          intensity={1}
          shadow-mapSize-width={1024}
          shadow-mapSize-height={1024}
          shadow-camera-far={100}
          shadow-camera-left={-10}
          shadow-camera-top={20}
          shadow-camera-right={20}
          shadow-camera-bottom={-10}
        />
        {/* poinLight can be positioned as sources of light */}
        <pointLight position={[-10, 0, 20]} intensity={0.5} />
        <pointLight position={[0, -10, 20]} intensity={1.5} />
        {/* ambient light doesn't cast shadows */}
        <ambientLight intensity={0.7} />
        <Sphere position={[0, 0, 0]} url={WauGradient} />

        {/* plane that receives casted shadow */}
        <group>
          <animated.mesh
            // enable receiving shadows
            receiveShadow
            rotation={[-Math.PI / 2, 0, 0]}
            position={planeAnim.position}
            >
            <planeBufferGeometry attach='geometry' args={[100, 100]} />
            
            <shadowMaterial attach='material' opacity={0.1} />
          </animated.mesh>
        </group>
      </AnimatedCanvas>
    </>
  )
}

export default IndexPage
